import PropTypes from 'prop-types'

import { styled } from '@mui/material/styles'

import { contrastColor } from '@/theme/utils'

const RootStyle = styled('span')(({ theme, ownerState }) => {
  const isLight = theme.palette.mode === 'light'
  const { color, variant } = ownerState

  const styleFilled = color => ({
    color: contrastColor(color) || theme.palette.text.primary,
    backgroundColor: color
  })

  const styleOutlined = color => ({
    color: contrastColor(color) || theme.palette.text.primary,
    backgroundColor: 'transparent',
    border: `1px solid ${color}`
  })

  const styleGhost = color => {
    const hexColor = color
    const colorText = contrastColor(hexColor)

    return {
      color: colorText,
      backgroundColor: color ? hexColor : 'inherit'
    }
  }
  return {
    height: 22,
    minWidth: 22,
    lineHeight: 0,
    borderRadius: 8,
    cursor: 'default',
    alignItems: 'center',
    whiteSpace: 'nowrap',
    display: 'inline-flex',
    justifyContent: 'center',
    padding: theme.spacing(0, 1),
    color: theme.palette.grey[800],
    fontSize: theme.typography.pxToRem(12),
    fontFamily: theme.typography.fontFamily,
    backgroundColor: theme.palette.grey[300],
    fontWeight: theme.typography.fontWeightBold,

    ...(color !== 'default'
      ? {
          ...(variant === 'filled' && { ...styleFilled(color) }),
          ...(variant === 'outlined' && { ...styleOutlined(color) }),
          ...(variant === 'ghost' && { ...styleGhost(color) })
        }
      : {
          ...(variant === 'outlined' && {
            backgroundColor: 'transparent',
            color: theme.palette.text.primary,
            border: `1px solid ${theme.palette.grey[500_32]}`
          }),
          ...(variant === 'ghost' && {
            color: isLight ? theme.palette.text.secondary : theme.palette.common.white,
            backgroundColor: theme.palette.grey[500_16]
          })
        })
  }
})

TicketCauseLabel.propTypes = {
  children: PropTypes.node,
  color: PropTypes.string,
  variant: PropTypes.oneOf(['filled', 'outlined', 'ghost'])
}

export function TicketCauseLabel({ color = 'default', variant = 'ghost', children, ...other }) {
  return (
    <RootStyle ownerState={{ color, variant }} {...other}>
      {children}
    </RootStyle>
  )
}
